import { Component, ViewEncapsulation } from '@angular/core';
import { AppConfig } from '../app.config';
import { UsersService } from './../services/UsersService';
@Component({
  selector: 'dashboard',
  providers:[UsersService],
  template: require('./dashboard.template.html'),
  styles: [require('./dashboard.style.scss'),require('./../widgets/widgets.style.scss')],
  encapsulation: ViewEncapsulation.None
})
export class Dashboard {
  config: any;
  month: any;
  year: any;
  TotalUsers = 0;
  FbUsers = 0;
  GoogleUsers = 0;
  LinkedINUsers = 0;
  TotalTodayUsers = 0;
  TotalTodayFbUsers = 0;
  TotalTodayGoogleUsers = 0;
  TotalTodayLinkedINUsers = 0;
  TodayEmailUsers = 0;
  EmailUsers = 0;
  public usersService:UsersService;
  constructor(config: AppConfig,  public _usersService:UsersService) {
    this.config = config.getConfig();
    _usersService.getUsersStats()
            .subscribe(
                a=> {
                        if(a.code == 200)
                        {
                            this.TotalUsers = a.data.TotalUsers;
                            this.FbUsers = a.data.FbUsers;
                            this.GoogleUsers = a.data.GoogleUsers;
                            this.LinkedINUsers = a.data.LinkedINUsers;
                            this.TotalTodayUsers = a.data.TotalTodayUsers;
                            this.TotalTodayFbUsers = a.data.TotalTodayFbUsers;
                            this.TotalTodayGoogleUsers = a.data.TotalTodayGoogleUsers;
                            this.TotalTodayLinkedINUsers = a.data.TotalTodayLinkedINUsers; 
                            this.EmailUsers = a.data.EmailUsers;
                            this.TodayEmailUsers = a.data.TodayEmailUsers;
                        }
                    });
                
  }

  ngOnInit(): void {
    let now = new Date();
    this.month = now.getMonth() + 1;
    this.year = now.getFullYear();
  }
}



